import * as yup from 'yup'
const CAMPO_REQUERIDO = '  obrigatrio'

export default yup.object().shape({
  senhaAtual: yup.string()
    .matches(/^\d+$/, 'Apenas nmero  permitido')
    .min(4, 'Senha Atual deve ter pelo menos 4 caracteres')
    .max(6, 'Senha Atual deve ter no mximo 6 caracteres')
    .required('Senha Atual' + CAMPO_REQUERIDO),    
  novaSenha: yup.string()
    .matches(/^\d+$/, 'Apenas nmero  permitido')
    .min(4, 'Nova Senha deve ter pelo menos 4 caracteres')
    .max(6, 'Nova Senha deve ter no mximo 6 caracteres')
    .required('Nova Senha' + CAMPO_REQUERIDO),
  confirmeSenha: yup.string()
    .matches(/^\d+$/, 'Apenas nmero  permitido')
    .min(4, 'Confirmao Senha deve ter pelo menos 4 caracteres')
    .max(6, 'Confirmao Senha deve ter no mximo 6 caracteres')
    .oneOf([yup.ref('novaSenha'), null], 'As senhas so diferentes')        
})