import * as yup from 'yup';
const CAMPO_REQUERIDO = '  obrigatrio';

export default yup.object().shape({
  tipoAgendamento: yup.string()
    .required('Tipo de Agendamento' + CAMPO_REQUERIDO),
  qtdeRepeticao: yup.number()
    .typeError('Qtde Repetio deve ser um nmero')
    .required('Qtde Repetio' + CAMPO_REQUERIDO),
  dataExecucao: yup.date()
    .typeError('Entre com uma data vlida')
    .required('Data Execuo' + CAMPO_REQUERIDO),
  horaExecucao: yup.string()
  .required('Hora Execuo' + CAMPO_REQUERIDO),
})